<template>
	<YPage :logo="require('@/assets/img/logo_white.png')">
		<div class="s-tool-wrapper">
			<div class="s-formatter-hd yu-flex-level">
				<div class="yu-flex-item s-title">JSON格式化</div>
				<t-select class="s-select" label="展开层级: " v-model="expandDepth" placeholder="请选择展开层级">
					<t-option v-for="item in 10" :value="item" :key="item">{{item}}</t-option>
				</t-select>
				<t-checkbox class="s-checkbox" v-model="sort">排序</t-checkbox>
				<t-checkbox class="s-checkbox" v-model="previewMode">预览</t-checkbox>
			</div>
			<div class="s-formatter-bd yu-flex">
				<div class="inp-box yu-flex-item">
					<textarea class="inp" v-model="jsonInput" @input="bindChangeValue"></textarea>
				</div>
				<div class="json-box yu-flex-item">
					<JsonViewer v-model="jsonParse" :expand-depth="expandDepth" :sort="sort" :preview-mode="previewMode" expanded :copyable="{copyText: '复制',copiedText:'已复制'}" :show-double-quotes="false" v-if="jsonParse"></JsonViewer>
					<p v-html="error"></p>
				</div>
			</div>
		</div>
	</YPage>
</template>
<script>
	import JsonViewer from 'vue-json-viewer'
	import YPage from '@/components/YPage.vue'
	import { Select as TSelect, Option as TOption, Checkbox as TCheckbox } from 'tdesign-vue';
	export default {
		components: {
			YPage,
			JsonViewer,
			TSelect,
			TOption,
			TCheckbox
		},
		data() {
			return {
				jsonInput: '{"version":"1.0","time":"2020-12-26","desc":"json格式化功能上线"}',
				jsonParse: '',
				expandDepth: 5,
				sort: false,
				previewMode: false,
				error: ''
			}
		},
		methods: {
			bindChangeValue(e) {
				console.log(e.target.value)
				try {
					let val = JSON.parse(e.target.value);
					this.jsonParse = val;
				} catch (error) {
					console.log(error)
					this.error = `Expecting 'NUMBER', 'NULL', 'BOOLEAN', '{}', '[]'`;
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.s-tool-wrapper {
		margin: 10px;
		background-color: #fff;
		.s-formatter-hd {
			padding: 20px 15px 5px;
			.s-title{
				font-size: 16px;
				line-height: 24px;
				font-weight: 500;
			}
			.s-select{
				width: 160px;
			}
			.s-checkbox{
				margin-left: 15px;
			}
		}

		.s-formatter-bd {
			.inp-box {
				padding: 15px;
				min-height: 500px;

				.inp {
					width: 100%;
					height: 100%;
					box-sizing: border-box;
					border: 1px solid #e6e6e6;
					padding: 15px;
					font-size: 14px;
					line-height: 24px;
				}
			}

			.json-box {
				margin: 15px;
				border: 1px solid #e6e6e6;
			}
		}
	}
</style>